import uuid from "react-uuid";
import isEqual from "lodash/isEqual";

import { useCallback, useEffect, useState } from "react";

import useDispatchAction from "hooks/useDispatchAction";

import { BasicButton, LoadingIndicator, Message } from "components";

import Movie from "./Movie";
import { useBreakpoints } from "contexts/ViewPortProvider";
import Icons from "icons";

const horizontal = new Set<string | undefined>(["mobile", "phablet"]);
const vertical = new Set<string | undefined>(["tablet", "desktop", "desktopHD"]);

type Orientation = "vertical" | "horizontal" | undefined;

const movieWidth = 270; // chyba faktycznie nieużywane bo jest small które je zastępuje kiedy trzeba
const movieHeight = 200;

const movieSmallWidth = 180;

function calculateMoviesNumber(window: number, movieSize: number) {
    return Math.floor(window / movieSize);
}

interface Props {
    isLoading: boolean;
    isError: boolean;
    errorMessage: string;
    movies: any[];
    renderCondition?: boolean;
}

const Slider = (props: Props) => {
    const { isError, isLoading, errorMessage, movies } = props;

    console.log("all movies in slider: ", movies.length);

    const { point: viewportType, width, height } = useBreakpoints();

    const [selected, setSelected] = useState<any>({});
    const [orientation, setOrientation] = useState<Orientation>(undefined);
    const [moviesNumber, setMoviesNumber] = useState<number>(0);
    const [firstMovie, setFirstMovie] = useState<number>(0);

    const showNext = useCallback(() => {
        if (firstMovie <= movies.length - 2 - moviesNumber) setFirstMovie(firstMovie => firstMovie + 1);
        else {
            setFirstMovie(0);
        }
    }, [firstMovie, moviesNumber]);

    const showPrevious = useCallback(() => {
        if (firstMovie !== 0) setFirstMovie(firstMovie - 1);
        else {
            setFirstMovie(movies.length - 1);
        }
    }, [firstMovie, moviesNumber]);

    const { setSelectedMovie } = useDispatchAction();

    const { clearError } = useDispatchAction();

    useEffect(() => {
        if (vertical.has(viewportType)) {
            if (orientation !== "vertical") {
                setOrientation("vertical");
            }
        }

        if (horizontal.has(viewportType)) {
            if (orientation !== "horizontal") {
                setOrientation("horizontal");
            }
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [viewportType, orientation]);

    useEffect(() => {
        let count = 0;

        switch (orientation) {
            case "horizontal":
                count = calculateMoviesNumber((width as number) - 40, movieSmallWidth);
                break;
            case "vertical":
                count = calculateMoviesNumber(height as number, movieHeight);
                break;
            default:
                break;
        }

        count && setMoviesNumber(count);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [width, height, orientation]);

    const handleClear = useCallback(() => {
        clearError();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const selectMovie = useCallback((movie: any) => {
        setSelected(movie);
    }, []);

    useEffect(() => {
        selected && setSelectedMovie(selected);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [selected]);

    const selectedMovies = movies.slice(firstMovie, moviesNumber + firstMovie);

    console.log("displayable films", selectedMovies);

    const previousButtonClass =
        orientation === "horizontal" ? "button--sidebar button--left" : "button--sidebar button--upwards";
    const nextButtonClass =
        orientation === "horizontal" ? "button--sidebar button--right" : "button--sidebar button--downwards";

    const sliderClass = orientation === "horizontal" ? "sidebar sidebar--horizontal" : "sidebar sidebar--vertical";

    return (
        <div className={sliderClass}>
            <BasicButton className={previousButtonClass} onClick={showPrevious}>
                <Icons.Left />
            </BasicButton>
            {isLoading && <LoadingIndicator />}
            {isError && <Message message={errorMessage} handleClear={handleClear} />}
            {selectedMovies.map(movie => {
                return (
                    <Movie
                        key={uuid()}
                        clickHandler={selectMovie}
                        movie={movie}
                        isSelected={isEqual(movie, selected)}
                    />
                );
            })}
            <BasicButton
                disabled={firstMovie >= movies.length - 2 - moviesNumber}
                className={nextButtonClass}
                onClick={showNext}
            >
                <Icons.Right />
            </BasicButton>
        </div>
    );
};

export default Slider;

