import { Video } from "types";

interface Props {
    clickHandler: React.MouseEventHandler<HTMLDivElement>;
    movie: any;
    isSelected: boolean;
}

//https://i3.ytimg.com/vi/iZhRbftPyG0/maxresdefault.jpg

const Movie = (props: Props) => {
    const { clickHandler, movie, isSelected } = props;

    //if (!(movie?.id?.videoID && movie?.snippet?.title)) return null;
    if (!Boolean(movie?.snippet?.title && movie.id.videoId)) return null;

    const src = "https://i3.ytimg.com/vi/" + movie.id.videoId + "/maxresdefault.jpg";
    const alt = "Image of " + movie.snippet.title;

    return (
        <div
            className={isSelected ? "movie selected" : "movie"}
            onClick={e => {
                e.stopPropagation();
                clickHandler(movie);
            }}
        >
            <div className="movie__picture">
                <img src={src} alt={alt} />
            </div>
            <div className="movie__title">{movie.snippet.title}</div>
        </div>
    );
};

export default Movie;
