import uuid from "react-uuid";
import isEqual from "lodash/isEqual";

import { useCallback, useEffect, useState } from "react";

import useDispatchAction from "hooks/useDispatchAction";

import { LoadingIndicator, Message } from "components";
import { Video } from "types";

import Movie from "./Movie";

interface Props {
    isLoading: boolean;
    isError: boolean;
    errorMessage: string;
    movies: Video[];
}
const SideBar = (props: Props) => {
    const { isError, isLoading, errorMessage, movies } = props;

    const [selected, setSelected] = useState<Video>({} as Video);
    const { setSelectedMovie } = useDispatchAction();
    const { clearError } = useDispatchAction();

    const handleClear = useCallback(() => {
        clearError();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const selectMovie = useCallback((movie: any) => {
        setSelected(movie);
    }, []);

    useEffect(() => {
        selected && setSelectedMovie(selected);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [selected]);

    return (
        <div className="sidebar">
            {isLoading && <LoadingIndicator />}
            {isError && <Message message={errorMessage} handleClear={handleClear} />}
            {movies.map(movie => {
                return (
                    <Movie
                        key={uuid()}
                        clickHandler={selectMovie}
                        movie={movie}
                        isSelected={isEqual(movie, selected)}
                    />
                );
            })}
        </div>
    );
};

export default SideBar;
