import YouTube from "react-youtube";

const options = {
    width: "100%",
    height: "100%",
    playerVars: {
        controls: 1,
        autoplay: 1,
        hl: "pl",
        modestbranding: 1,
    },
};

interface Props {
    video: any;
}

export const Player = (props: Props) => {
    const { video } = props;
    if (!video) return null;
    return video && <YouTube videoId={video} opts={options}></YouTube>;
};
